.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.Status_part {
  flex: none;
  width: 60%;
  height: 100%;
  text-align: center;
}

.Control_part {
  flex: none;
  width: 40%;
  height: 100%;
  text-align: center;
}

.Status_area {
  width: 80%;
  height: 100%;
  max-height: 100%;
  padding: 0 50px;
  overflow: auto;
}

.Status_text {
  overflow: auto;
  width: 90%;
  max-width: 90%;
  height: 70vh;
  max-height: 70vh;
  background-color: rgb(0, 0, 0);
  font-size: 17px;
  text-align: left;
}

.Control_area {
  padding: 50px;
  text-align: left;
}

.hide {
  display: none;
}

.Status_text {
  padding: 30px;
}
